.promote-square {
    width: 50%;
    height: 50%;
  }
  .piece-container {
    cursor: grab;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .piece {
    max-width: 70%;
    max-height: 70%;
  }
  .board {
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
  }
  .cursor-pointer {
    cursor: pointer;
  }